import type { Meta, StoryFn } from '@storybook/web-components';

import '@tiger-analytics/ui';

import { html } from 'lit';

export default {
  title: 'Grid/ta-grid-container',
  component: 'ta-grid-container',
  parameters: {
    controls: { expanded: true },
  },
  argTypes: {
    showSideMargin: {
      control: { type: 'boolean' },
      description: 'when true, adds left and right margin to the container.',
      table: {
        defaultValue: { summary: 'false' },
      },
    },
    restrictHeightToMaxContent: {
      control: { type: 'boolean' },
      description:
        'by default container occupies available height. mark this as true in case you want to restrict it to its content.',
      table: {
        defaultValue: { summary: 'false' },
      },
    },
  },
} as Meta;

const description = `The <ta-grid-container> web component creates a 12-column grid layout, serving as a container for grid-based content. It allows for responsive and flexible design by automatically distributing available space into 12 equal-width columns. Additionally, <ta-grid-container> can contain other <ta-grid-container> elements as child components, allowing for nested grid layouts. This makes it easy to structure complex layouts with multiple levels of nested grids, ensuring a consistent and scalable design approach.`;

const Template: StoryFn = (args) => html`
  <ta-grid-container
    style="background: yellowgreen;"
    .showSideMargin="${args.showSideMargin}"
    .restrictHeightToMaxContent="${args.restrictHeightToMaxContent}">
    Container</ta-grid-container
  >
  <ta-grid-container .showSideMargin="${false}">
    <ta-grid-column
      ><div class="story-title">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</div></ta-grid-column
    >
    <ta-grid-column><div class="story-description">${description}</div></ta-grid-column>
  </ta-grid-container>
`;

export const CodeAndAccessibility = Template.bind({});
CodeAndAccessibility.args = {
  showSideMargin: false,
  restrictHeightToMaxContent: false,
};
